import { Button, Card, Col, Descriptions, message, Radio, Result, Row, Steps, Typography } from 'antd';
import React, { useState } from 'react';
import Thumbnail from './Thumbnail.jpg';
import { STOCK } from './App';

const STEPS = [
  '',
  '',
  '',
  '',
];

function Preparation({ trades, groupID, handleStart }) {
  const [stock, selectStock] = useState();
  const [current, setCurrent] = useState(0);
  const next = () => {
    if (current >= 1 && !stock) {
      if (groupID !== 10) {
        current !== 1 && setCurrent(1);
        return message.warning('');
      }
      const stocks = Object.keys(STOCK).filter((s) => trades.findIndex((trade) => trade.stock === s) === -1);
      selectStock(stocks[Math.floor(Math.random() * stocks.length)]);
    }
    if (current >= 3) {
      return handleStart(stock);
    }
    current < STEPS.length - 1 && setCurrent(current + 1);
  };
  const prev = () => {
    current > 0 && setCurrent(current - 1);
  };

  return (
    <div id="preparation">
      <Steps current={current}>
        {
          STEPS.map((item) => (
            <Steps.Step key={item} title={item} />
          ))
        }
      </Steps>
      {
        (() => {
          if (current === 0) {
            return (
              <Result
                title={`${trades.length + 1}`}
                subTitle=""
                extra={<Button type="primary" onClick={next}></Button>}
              />
            );
          }

          if (current === 1 || current === 2) {
            return (
              <div id="step">
                <Row justify="space-around">
                  {
                    Object.entries(STOCK).map(([key, title]) => {
                      const unselected = trades.findIndex((trade) => trade.stock === key) === -1;
                      return (
                        <Col key={key} span={4}>
                          <Card
                            hoverable
                            cover={<img alt="Thumbnail" src={Thumbnail} />}
                            onClick={() => current === 1 && groupID !== 10 && unselected && selectStock(key)}
                          >
                            <Radio
                              checked={stock === key}
                              disabled={current === 2 || !unselected || groupID === 10}
                            >{`${title}`}</Radio>
                          </Card>
                        </Col>
                      );
                    })
                  }
                </Row>
                <div className="introduce">
                  {
                    current === 1 ? (
                      groupID === 10 ?
                        <Typography>
                          <Typography.Paragraph>
                            
                            <Typography.Text keyboard>Point0</Typography.Text>
                            
                            <Typography.Text keyboard>Point0</Typography.Text>
                            
                          </Typography.Paragraph>
                          <Typography.Paragraph>
                            
                            <Typography.Text strong></Typography.Text>
                            
                            <Typography.Text keyboard>Point0</Typography.Text>
                            /
                            <Typography.Text strong>
                            <Typography.Text keyboard>300</Typography.Text>
                            </Typography.Text>
                            
                          </Typography.Paragraph>
                          <Typography.Paragraph>
                            <Typography.Text strong>
                              
                              <Typography.Text keyboard>5000</Typography.Text>
                              
                            </Typography.Text>
                          </Typography.Paragraph>
                        </Typography> : (
                          groupID === 20 ?
                            <Typography>
                              <Typography.Paragraph>
                                
                                <Typography.Text keyboard>Point0</Typography.Text>
                                
                                <Typography.Text keyboard>Point0</Typography.Text>
                                
                              </Typography.Paragraph>
                              <Typography.Paragraph>
                                
                                <Typography.Text strong></Typography.Text>
                                
                                <Typography.Text keyboard>Point0</Typography.Text>
                                /
                                <Typography.Text strong>
                                <Typography.Text keyboard>300</Typography.Text>
                                </Typography.Text>
                                
                              </Typography.Paragraph>
                              <Typography.Paragraph>
                                
                                <Typography.Text keyboard>5000</Typography.Text>
                                
                                <Typography.Text strong>
                                  
                                </Typography.Text>
                                
                              </Typography.Paragraph>
                              <Typography.Paragraph>
                                
                                <Typography.Text keyboard>10</Typography.Text>
                                
                                <Typography.Text keyboard>300</Typography.Text>
                                
                              </Typography.Paragraph>
                            </Typography> : (
                              groupID === 30 &&
                              <Typography>
                                <Typography.Paragraph>
                                  
                                  <Typography.Text keyboard>Point0</Typography.Text>
                                  
                                  <Typography.Text strong>
                                    
                                  </Typography.Text>
                                </Typography.Paragraph>
                                <Typography.Paragraph>
                                  
                                  <Typography.Text keyboard>Point0</Typography.Text>
                                  
                                </Typography.Paragraph>
                                <Typography.Paragraph>
                                  
                                  <Typography.Text strong></Typography.Text>
                                  
                                  <Typography.Text keyboard>Point0</Typography.Text>
                                  /
                                  <Typography.Text strong>
                                  <Typography.Text keyboard>300</Typography.Text>
                                  </Typography.Text>
                                  
                                </Typography.Paragraph>
                                <Typography.Paragraph>
                                  
                                  <Typography.Text keyboard>5000</Typography.Text>
                                  
                                  <Typography.Text keyboard>10</Typography.Text>
                                  
                                  <Typography.Text strong></Typography.Text>
                                  
                                  <Typography.Text strong></Typography.Text>
                                  
                                </Typography.Paragraph>
                                <Typography.Paragraph>
                                  
                                  <Typography.Text keyboard>300</Typography.Text>
                                  
                                </Typography.Paragraph>
                              </Typography>
                            )
                        )
                    ) : (
                      groupID === 10 ?
                        <Typography>
                          <Typography.Paragraph>
                            <Typography.Text strong>
                              
                              <Typography.Text mark>
                                
                                <Typography.Text keyboard>300</Typography.Text>
                                {STOCK[stock]}
                              </Typography.Text>
                              
                              <Typography.Text keyboard>10</Typography.Text>
                              
                            </Typography.Text>
                          </Typography.Paragraph>
                          <Typography.Paragraph>
                            
                          </Typography.Paragraph>
                        </Typography> : (
                          groupID === 20 ?
                            <Typography>
                              <Typography.Paragraph>
                                <Typography.Text strong>
                                  
                                  <Typography.Text keyboard>300</Typography.Text>
                                  {STOCK[stock]}
                                  <Typography.Text keyboard>10</Typography.Text>
                                  
                                </Typography.Text>
                              </Typography.Paragraph>
                              <Typography.Paragraph>
                                
                              </Typography.Paragraph>
                            </Typography> : (
                              groupID === 30 &&
                              <Typography>
                                <Typography.Paragraph>
                                  <Typography.Text strong>
                                    
                                    <Typography.Text keyboard>300</Typography.Text>
                                    {STOCK[stock]}
                                    <Typography.Text keyboard>10</Typography.Text>
                                    
                                  </Typography.Text>
                                </Typography.Paragraph>
                                <Typography.Paragraph>
                                  
                                </Typography.Paragraph>
                              </Typography>
                            )
                        )
                    )
                  }
                </div>
                {
                  current === 1 ?
                  <Descriptions bordered column={1} title="">
                    <Descriptions.Item label="">510</Descriptions.Item>
                    <Descriptions.Item label="">0</Descriptions.Item>
                    <Descriptions.Item label="">0</Descriptions.Item>
                    <Descriptions.Item label="">0</Descriptions.Item>
                    <Descriptions.Item label="">0</Descriptions.Item>
                    <Descriptions.Item label="">5000</Descriptions.Item>
                    <Descriptions.Item label="">5000</Descriptions.Item>
                  </Descriptions> :
                  <Descriptions bordered column={1} title="">
                    <Descriptions.Item label="">10.0</Descriptions.Item>
                    <Descriptions.Item label="">10.0</Descriptions.Item>
                    <Descriptions.Item label="">300</Descriptions.Item>
                    <Descriptions.Item label="">0</Descriptions.Item>
                    <Descriptions.Item label="">0</Descriptions.Item>
                    <Descriptions.Item label="">2000</Descriptions.Item>
                    <Descriptions.Item label="">3000</Descriptions.Item>
                    <Descriptions.Item label="">5000</Descriptions.Item>
                  </Descriptions>
                }
              </div>
            );
          }

          return (
            <div id="step">
              <Typography>
                <Typography.Title level={5}>
                  {STOCK[stock]}
                  <Typography.Text keyboard>300</Typography.Text>
                  
                  <Typography.Text keyboard>2000</Typography.Text>
                  
                  <Typography.Text keyboard>20</Typography.Text>
                  
                  <Typography.Text keyboard>Point 1</Typography.Text>
                  
                  <Typography.Text keyboard>Point 20</Typography.Text>
                  
                </Typography.Title>
                <Typography.Title level={5}>
                    
                </Typography.Title>
              </Typography>
            </div>
          )
        })()
      }
      <Row justify="center">
        <Col span={4}>
          <Button type="primary" onClick={next}>
            {
              current < STEPS.length - 1 ? '' : ''
            }
          </Button>
        </Col>
        <Col span={4}>
          <Button disabled={current === 0} onClick={prev}></Button>
        </Col>
      </Row>
    </div>
  );
}

export default Preparation;
